
import logo from '../svg/mainLogo.svg';
import SuccessMsg from './SuccessMsg';
import { React,useContext } from 'react';
import { Context } from '../App';
import FormInput from './FormInput';

const Register = () =>{
    
    const {updateRegister,setRegister,regStatus} = useContext(Context);
    
    return (
            <div className='text-center login-form'>
                <form onSubmit={updateRegister}>
                <img src={logo} height='100vh' alt="img"></img>
                <h1 class="h3 mb-3 fw-bolder">Welcome, We See That It's Your First Time Here</h1>
                <p className='lead'>Please Enter Your Credentials Below To Register</p>
                

                <FormInput type={'text'} name={'name'}
                        placeholder={'password'} label={"Full Name"} 
                        errorMessage={"Please enter your Full Name"} />

                <FormInput type={'email'} name={'email'}
                        placeholder={'email'} label={"Email"} 
                        errorMessage={"Please enter your Email"} />

                 <FormInput type={'password'} name={'password'}
                        placeholder={'password'} label={"Password"} 
                        errorMessage={"Please enter your Password"} />

                <FormInput type={'text'} name={'mobile'}
                            placeholder={'mobile'} label={"Mobile"} 
                            errorMessage={"Please enter your Mobile Number"}  />

                


                <div className='d-flex flex-row justify-content-center mt-5'>
                    <button class=" btn btn-lg btn-primary me-3" onClick={()=> setRegister(false)}>Cancel</button>
                    <button class=" btn btn-lg btn-primary" type="submit">Sign In</button>
                </div>
                    <p class="mt-5 mb-3 text-muted">© Pet Heaven 2023</p>
                </form>
       
                {regStatus && <SuccessMsg keyword={"Registration"}/>}
            </div>
       
    )
}

export default Register;