import bg from '../images/bg-full.png';
import ob from '../images/ob.png';
import jc from '../images/jc.png';

import React from 'react';
import Carousel from 'react-bootstrap/Carousel';
import Image from 'react-bootstrap/Image';

import Container from 'react-bootstrap/Container';

const Testimonials = ()=>
{
 return (
  <Container className='mt-5 pt-5 testimonials shadow-lg'>
        <h1 className='text-center border-bottom pb-3 mt-3 fw-bold' >Testimonials</h1>
        <Carousel  fade>
            <Carousel.Item>
            <Image src={bg} fluid/>
            </Carousel.Item>

        <Carousel.Item>
            <Image src={ob} fluid/>
        </Carousel.Item>

        <Carousel.Item>
            <Image src={jc} fluid />
        </Carousel.Item>
        </Carousel>

        </Container>




    
 )
};

export default Testimonials;






