import React from 'react';
import Testimonials from './Testimonials';
import CrossInfo from './CrossInfo';
import LocateUs from './LocateUs';
import Footer from './Footer';
import transition from './transition';
import rightArrow from '../svg/rightArrow.svg';
import nut from '../svg/nut.svg';
import vet from '../svg/vet.svg';
import clean from '../svg/clean.svg';
import dogRun from '../svg/dogrun.svg';

const About=()=>{


return (

    <div className='about'>
        <CrossInfo/>

    <div class="container px-4 py-5 shadow mt-4 ">
    <h2 class="pb-2 border-bottom shadow">Pet Caring And Advice</h2>

    <div class="row row-cols-1 row-cols-md-2 align-items-md-center g-5 py-5">
      <div class="col d-flex flex-column align-items-start gap-2">
        <h2 class="fw-bold">How We Do It</h2>
        <p>This is how We Care For The Pets in Pet Heaven. <br/> 
        Should you wish to adopt a furry buddy from us, you can try <br/> 
        The following tips on the right side of this panel</p>
        <img className='align-items-center' src={rightArrow} width="100vw" height="70vh"></img>
      </div>

      <div class="col">
        <div class="row row-cols-1 row-cols-sm-2 g-4">
          <div class="col d-flex flex-column gap-2">
            <div class="feature-icon-small d-inline-flex align-items-center justify-content-start fs-4 rounded-3">
            <img src={nut} height="40vh" ></img>
            </div>
            
            <h4 class="fw-semibold mb-0 text-body-emphasis">Proper Nutrition</h4>
            <p class="text-body-secondary">We make sure that the food consumed by the pets are approved by the vets</p>
          </div>

          <div class="col d-flex flex-column gap-2">
            <div class="feature-icon-small d-inline-flex align-items-center justify-content-start fs-4 rounded-3">
            <img src={vet} height="40vh" ></img>
            </div>
            <h4 class="fw-semibold mb-0 text-body-emphasis">Checkup By vets</h4>
            <p class="text-body-secondary">We schedule checkups by volunteer vets every week, on Friday.</p>
          </div>

          <div class="col d-flex flex-column gap-2">
            <div class="feature-icon-small d-inline-flex align-items-center justify-content-start fs-4 rounded-3">
            <img src={clean} height="40vh" ></img>
            </div>
            <h4 class="fw-semibold mb-0 text-body-emphasis">Regular Cleaning Of Enclosures</h4>
            <p class="text-body-secondary">We have a regular cleaning schedule of the Enclosure, at least once every 2 weeks. This reduces the chances of diseases from spreading.</p>
          </div>

          <div class="col d-flex flex-column gap-2">
            <div class="feature-icon-small d-inline-flex align-items-center justify-content-start  fs-4 rounded-3">
            <img src={dogRun} height="40vh" ></img>
            </div>
            <h4 class="fw-semibold mb-0 text-body-emphasis">Pet Exercise</h4>
            <p class="text-body-secondary">Our Adopted Pets Are always Running Around Freely in Our Enclosed Parameters. They will have the chance to exercise as and when they wish.</p>
          </div>
        </div>
      </div>
    </div>
  </div>











        <Testimonials/>
        <LocateUs/>
        <Footer/>
    </div>






)
}

export default transition(About);