import React from 'react';
import logo from '../svg/mainLogo.svg';
import SuccessMsg from './SuccessMsg';
import { useContext } from 'react';
import { Context } from '../App';
import FormInput from './FormInput';
import { useNavigate } from "react-router-dom";
const Login = () =>{

    
    const {setLogin,loginStatus,updateLogin} = useContext(Context);
    const navigate = useNavigate();
    const redirect = () => {
        navigate('/')
      }
    
   


    return (
       

            <div className='text-center login-form'>
                <form onSubmit={updateLogin}>
                    <img src={logo} height='100vh' alt="img"></img>
                    <h1 class="h3 mb-3 fw-bolder">Welcome Back, Fellow Pet Lover!</h1>
                    <p className='lead'>Please Enter Your Credentials Below To Login</p>

                      <FormInput type={'email'} name={'email'}
                        placeholder={'email'} label={"Email"} 
                        errorMessage={"Please enter your Email"} />

                     <FormInput type={'password'} name={'password'}
                        placeholder={'password'} label={"Password"} 
                        errorMessage={"Please enter your Password"} />

           
                    <div className='d-flex flex-row justify-content-center mt-5'>
                        <button class=" btn btn-lg buttonColor btn-outline-light me-3" onClick={()=> setLogin(false)}>Cancel</button>
                        <button class=" btn btn-lg buttonColor btn-outline-light" type="submit" onClick={redirect} >Sign In</button>
                    </div>
                        <p class="mt-5 mb-3 text-muted">© Pet Heaven 2023</p>
                </form>
                {loginStatus && <SuccessMsg keyword={"Login"}/>}
            </div>
       
    )
}

export default Login;

