import {React,useContext} from "react";
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import { Link } from 'react-router-dom';
import { Context } from '../App';
import Container from 'react-bootstrap/Container';
import dogSide from '../images/dog-side.png';
import catSide from '../images/cat-side.png';
import logo from '../svg/mainLogo.svg';


const HomeHeader = ()=>{

    const {setLogin,setRegister,loginStatus} = useContext(Context);

    return(
   
      <div>

        {!loginStatus && 
            <Row className='px-4 mt-5 pb-5'>
                <Col sm={7} className="d-flex flex-row  ">
                 
                </Col>
                <Col sm={5} className='d-flex flex-column mt-5 msg-across'>
                    <h1 class='fw-bolder text-center mt-5'>Adopt A New Furry Friend Today!</h1>
                    <p class="lead mt-4 text-center">
                        Cats or Dogs, These Furry Angels Will Accompany You, Rainy And Sunnny Days.
                        There is Always A Friend In You And Me!
                    </p>
                    <div className='d-grid gap-2 d-md-flex justify-content-sm-center'>
                     <Button className="btn btn-lg btn-outline-light px-4  buttonColor" onClick={()=>{setRegister(true);}}>Register Now</Button>

                    <Button className='btn btn-lg btn-outline-light px-4  buttonColor openLoginBtn' onClick={()=>{setLogin(true);}}>Login Now</Button>

                   
                    </div>
                </Col>
            </Row>}


        {loginStatus &&
        <Row className="home-head">
            
           
            <Col md={6}>
            <Link as={Link} to='/showCat'>
                <Container className="petSideBox d-flex flex-column justify-content-center mt-3 text-center">
                    <img src={catSide} alt="catside"></img>
                </Container>
            </Link>
            </Col>
          


            <Col md={6}>
            <Link as={Link} to='/showDog'>
                <Container className="petSideBox d-flex flex-column justify-content-center text-center mt-3">
                    <img src={dogSide} alt="dogside"></img>
                </Container>
            </Link>
            </Col>
         

        </Row>

        }

      </div>

    )
}

export default HomeHeader;