import { useState } from 'react';
import Button from 'react-bootstrap/Button';
import Card from 'react-bootstrap/Card';
import Col from 'react-bootstrap/Col';
import ReactCardFlip from 'react-card-flip';
import { useContext } from 'react';
import { Context } from '../App';



const MiniCard=(props)=> {

  const {setPets} = useContext(Context);

  const [isFlipped,setFlipped] = useState(false);

  const chosen = {"name":props.name,
                  "image":props.image,
                  "des":props.des};

  function flipCard(){
    setFlipped(!isFlipped);
  }

  


  function updatePet()
  {
    setPets(selectedPets=>[...selectedPets,chosen]);
  
  }


  return (

    <Col md={3} className='d-flex align-items-center justify-content-center mt-5 text-center shadow card-box rounded'>
      <ReactCardFlip flipDirection='horizontal'  isFlipped={isFlipped}>
      <div className='cardFront' onMouseEnter={flipCard}>
        <Card className='cf' style={{ width: '20rem',height:'40vh'}}>
          <Card.Img variant="top" className='image-mini' src={props.image} />
          <Card.Body>
            <Card.Title>{props.name}</Card.Title>
            
          </Card.Body>
        </Card>
      </div>


      

      <div className='cardBack' onMouseLeave={flipCard} style={props.animal==='Cat'?{'backgroundColor':'#0093E9','backgroundImage':'linear-gradient(160deg, #0093E9 0%, #80D0C7 100%)'}:
                                                                                     {'backgroundImage':'linear-gradient( 135deg, #F05F57 10%, #360940 100%)'}}>
        <Card.Text className='cardBack fw-bold'>
            <small>{props.des}</small>  
              
        {props.inShowPet && <Button variant="primary" className='mt-3' type="button" onClick={updatePet}>Adopt Me</Button>}
        </Card.Text>
        
       
      </div>
      </ReactCardFlip>
      
     
    </Col>
 
  );
}

export default MiniCard;