
import React from 'react';
import CentralForm from './CentralForm';
import MyFooter from './Footer';
import transition from './transition';
const ReleasePet = ()=>{
    return (
        <div className='release-pet'>

             <div class="release-head position-relative p-3 my-5 p-md-5 ">
                <div class="col-md-5 p-lg-5 mx-auto my-5">

                    <h1 class="display-4 fw-bold ">We Know It's Never Easy To Let Go</h1>
                    <p class="lead">But We Will Be Sure To Care For Your Pet In The Future</p>   
                </div>
            </div>

            <CentralForm position={'relative'} heading={'Release Pet'}/>

            <MyFooter/>
        </div>
    )
}


export default transition(ReleasePet);