import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import locationsvg from '../svg/location.svg';
import Image from 'react-bootstrap/Image';

const LocateUs = ()=>{
    return (
        <Container className='mt-4 pt-5 pb-5 mb-5'>
             <h1 className='text-center mb-md-5 pb-2 border-bottom fw-bold '>Find Us</h1>
            <Row>   
                <Col md={6} className='lh-1 d-flex flex-column justify-content-center shadow-lg'>
                    <div className='location-words'>
                    <Image src={locationsvg} className='w-25' fluid></Image>
                    <h1 className='fw-bolder'>Our Location:</h1>
                    <h3>Pet Heaven</h3>
                    <p className='fw-bold'>50 Sungei Tengah Rd, Singapore 699012</p>
                    <p className='fw-bold'>Opening Hours: 8:30am to 9pm</p>
                    <p className='fw-bold'>Tel:+65 1234 4490</p>
                    <p className='fw-bold'>Email: enquiries@petheaven.org.sg</p>
                    </div>
                </Col>
                <Col md={6} >
                <iframe src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d15954.641234696819!2d103.7321005!3d1.3804734!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x31da1797148efe85%3A0x198673c4e23c5926!2sSociety%20for%20the%20Prevention%20of%20Cruelty%20to%20Animals%20(SPCA)!5e0!3m2!1sen!2ssg!4v1699847217673!5m2!1sen!2ssg" width="600" height="450" allowfullscreen="" loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
                </Col>
            </Row>
        </Container>
  
    )
}

export default LocateUs;