import {React,useContext} from 'react';
import Resource from '../Resource';
import MiniCard from './MiniCard';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import TopHeader from './TopHeader';
import dogSith from '../images/dog-sith.jpg';
import MyFooter from './Footer';
import transition from './transition';
import { Context } from '../App';
import tick from '../svg/tick.svg';

const ShowDog=()=>{
    const webUrl = 'https://api.thedogapi.com/v1/images/search?limit=10&has_breeds=1&api_key=live_Z8RAevRO7V07ggSWvaRH5mCxdfySyWKNRqbDBFMKwiiowkLjUZ0Mha2brZfeu9xO';
    const inShowDog = true;
    const {itemAdded} = useContext(Context);

    const render = (data)=>{

        if (data.loading===true) return <p>Loading.....</p>     
        console.log('got the data');

    
    
    return (


    <div>

        <TopHeader animal={"Dog"} image={dogSith} />
        <Container className='showPets'>
            <Row>
            {data.trans.map((dog)=>(
                <MiniCard animal={'dog'} name={dog.breeds[0].name} des={dog.breeds[0].temperament} image={dog.url} inShowPet={inShowDog}/>
            ))}
            </Row>
        </Container>
                {itemAdded && 
                
                <Container className='item-added'>
                    <img src={tick} height="40vh"></img>
                    <h1>
                        Pet Added To Adoption Page
                    </h1>
                </Container>}



        <MyFooter/>
    </div>
    )
}


return (
    <div>
     
        <Resource path={webUrl}render = {render}/>
    </div>
)
}

export default transition(ShowDog);

