import NavBar from './components/NavBar_JS';
import './styles.css';
import ShowDog from './components/Dogs_JS';
import ShowCat from './components/Cats_JS';
import About from './components/About_JS';
import Home from './components/Home_JS';
import {React, useState,createContext,useEffect} from 'react';
import {BrowserRouter as Router,  Route, Routes} from 'react-router-dom';
import AdoptionPage from './components/AdoptionPage';
import ReleasePet from './components/ReleasePet';
import { AnimatePresence } from 'framer-motion';
import ScrollToTop from './components/ScrollToTop';


export const Context = createContext();

function App() {

   const [openLogin,setLogin] = useState(false);
   const [openRegister,setRegister] = useState(false);
   const [loginStatus,setLoginStatus] = useState(false);
   const [regStatus,setRegStatus] = useState(false);
   const [selectedPets,setPets] = useState([]);
   const [itemAdded,setItemAdded] = useState(false);
   

   function updateLogin(event)
    {
           
        setLoginStatus(true);
        event.preventDefault();
        setTimeout(()=>{setLogin(false);}, 3000);
        
      
      
    }

    function updateRegister(event)
    {
        setRegStatus(!regStatus);
        event.preventDefault();
        setTimeout(()=>{setRegister(false); setLogin(true);
                          setRegStatus(false)}, 3000);
       
    }

   
    function updateItemAdded()
    {
      setItemAdded(true);
      setTimeout(()=>{setItemAdded(false);}, 1000);
    }


    useEffect(()=>{updateItemAdded()},[selectedPets]);


  return (

    <>
    <Router>
      <ScrollToTop/>
      <Context.Provider value={{openLogin,setLogin,openRegister,setRegister,
                                loginStatus,updateLogin,
                                regStatus,updateRegister,selectedPets,setPets,
                                itemAdded,setItemAdded}}>

      <NavBar />
    
      <div className='bgf'>
        <AnimatePresence mode="wait">
          <Routes>
            <Route path='/' exact element={ <Home /> } />
            <Route path='/about' element={ <About /> } />
            <Route path='/showCat' element={ <ShowCat /> } />
            <Route path='/showDog' element={ <ShowDog/> } />
            <Route path='/AdoptionPage' element={<AdoptionPage/>}/>
            <Route path='/ReleasePet' element={<ReleasePet/>}/>
          </Routes>
        </AnimatePresence>
      </div>
      </Context.Provider>
      </Router>
    </>

   
  );
}

export default App;
