import { React, useContext }  from 'react';
import Row from 'react-bootstrap/Row';
import Features from './Features';
import MyFooter from './Footer';


import town from '../images/town.jpg';
import corgi from '../images/corgi.jpg';

import { Context } from '../App';
import HowToCard from './HowToCard';
import darkCat from '../images/darkcat.jpg';
import coolDog2 from '../images/coolDog2.jpg'
import coolCat from '../images/coolCat.jpg';
import InfiniteScroll from './InfiniteScroll';
import HomeHeader from './HomeHeader';
import { Parallax } from 'react-parallax';

import transition from './transition';

const Home=()=>{

  const {loginStatus} = useContext(Context);

return (

    <div className='homeBackground'>

      
      <Parallax strength={600} bgImage={town} >
        <div className='home-head'>
        <HomeHeader loginStatus={loginStatus}/>
        </div>
     </Parallax>

      <Parallax strength={600}>
        
        <Row className='features'>
        <Features/>
        </Row>
      </Parallax>


      <Parallax strength={700} bgImage={corgi}>
      <div className='endorse-box'>
      <h1 className='text-center mb-5 mt-5 fw-bolder'>Endorsed By :</h1>
      <div class="logos mb-5">
      <InfiniteScroll/>
      <InfiniteScroll/>
      </div>
      </div>
      </Parallax>



      <Parallax strength={600} >
      <h1 className='mt-4 pt-4 pb-3 mb-5 text-center sub-color fw-bolder'>Steps To Adopt:</h1> 
      <Row className='py-5 ps-2 pe-2 '>
      <HowToCard Background={darkCat} header={"Browse Our Adoption Gallery"} para={"Find The Friend You Need And Fill The Form!"}/>
      <HowToCard Background={coolCat} header={"Book an appointment"} para={"All visits are strictly by appointment and slots will only be released 7 days beforehand."}/>
      <HowToCard Background={coolDog2} header={"Show up for your appointment on time"} para={"Please present your ID or show the confirmation email at Pet Heaven for verification purposes"}/>
       
      </Row>  
      </Parallax>


      <Parallax strength={-200}>
      
      <MyFooter/>
      </Parallax>
   
    </div>
   
  
    
)
}

export default transition(Home);





