import Container from 'react-bootstrap/Container';
import Nav from 'react-bootstrap/Nav';
import Navbar from 'react-bootstrap/Navbar';
import { Link } from 'react-router-dom';
import mainLogo from '../svg/mainLogo.svg';
import Login from './Login';
import Button from 'react-bootstrap/Button';
import Register from './Register';
import { useContext } from 'react';
import { Context } from '../App';


function Navbar_JS() {

  
  const {openLogin,setLogin,openRegister,setRegister,loginStatus} = useContext(Context);

  return (

    <div>
    <Navbar expand="lg" className="nav-cust" fixed="top">
      <Container>
        <Navbar.Brand as={Link} to='/'>
          <img src={mainLogo} width="40vw" alt="img"></img>
          Pet Heaven
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="me-auto">
          
          <Nav.Link as={Link} to='/' exact>Home</Nav.Link>
          <Nav.Link as={Link} to='/about' exact>About</Nav.Link>
          <Nav.Link as={Link} to='/showCat' exact>Cats Gallery</Nav.Link>
          <Nav.Link as={Link} to='/showDog' exact>Dogs Gallery</Nav.Link>
          <Nav.Link as={Link} to='/AdoptionPage' exact>Adoption</Nav.Link>
          <Nav.Link as={Link} to='/ReleasePet' exact>Release Pet</Nav.Link>
          </Nav>

          <div className='text-end topButtons'>
            {!loginStatus && <Button className='me-3 buttonColor btn-outline-light'  onClick={()=>{setRegister(true);
                                                                      setLogin(false);}}>Register</Button>}

           {!loginStatus && <Button className='buttonColor btn-outline-light' onClick={()=>{setLogin(true);
                                                setRegister(false)}}>Login</Button> }
         </div>
        </Navbar.Collapse>
      </Container>
    </Navbar>

    {openRegister && <Register/>}
    {openLogin && <Login/>}

    </div>
  );
}

export default Navbar_JS;