import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import catAndDog from '../images/cat-and-dog.jpg';
import Image from 'react-bootstrap/Image';
import mental from '../images/mental-well.jpg';
import React from 'react';

const CrossInfo=()=>{

    return (
        <Container className='pt-5 mt-3' fluid>
        <Row >
            <Col md={6} className='ps-0'>
            <Image src={mental} className='aboutImages shadow' fluid></Image>
            </Col>

            <Col md={6} className='mt-md-5 pt-5 shadow border-bottom'>
            <h1 class='fw-bolder text-center pt-md-5'>Our Mission</h1>
            <p class="fw-normal mt-md-4 text-center">
                We Take In Abandoned Pets And Care for them Around the clock,<br></br>
                accompanying them until they find their next owner.
            </p>
            </Col>
        <Row />
           <Row>
            <Col md={6} className='mt-5 pt-5 shadow'>
            <h1 class='fw-bolder text-center pt-md-5'>Why Adopt With Us:</h1>
            <p class="fw-normal mt-md-4 text-center">
                Humans, pets, we all deserved to be loved. Our furry friends will make <br></br>
                sure to deliver the tiny packets of love to you in your darkest days. <br></br>

            </p>
            </Col>

            <Col md={6} className='ps-0 pe-0 '>
            <Image src={catAndDog} className='aboutImages' fluid></Image>
            </Col>
            </Row>

        </Row>
    </Container>
    )
}


export default CrossInfo;