import {React, useState,useContext } from 'react';
import Resource from '../Resource';
import MiniCard from './MiniCard';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import cuteCat from '../images/cuteCat.png';
import TopHeader from './TopHeader';
import MyFooter from './Footer';
import transition from './transition';
import { Context } from '../App';
import tick from '../svg/tick.svg';



const ShowCat=()=>{
    const webUrl = 'https://api.thecatapi.com/v1/images/search?limit=10&has_breeds=1&api_key=live_Wjcj8eRKg2K3dXdHft5DC1qEUXa1o9oVS7PPj0Laoy0JI2vj9T0gBuAbQvu6xShK';

    const [openForm,setForm] = useState(false);
    const inShowCat = true;
    const {itemAdded} = useContext(Context);

    function setUpForm(event)
    {
    
        setForm(!openForm);
    }

    const render = (data)=>{
        if (data.loading===true) return <p>Loading.....</p>
        console.log('got the data');

    return (
        <div>
   
        
        <TopHeader animal={"Cat"} image={cuteCat} />
        
        <Container className='showPets'>
            <Row>
                {data.trans.map((cat)=>(
                <MiniCard name={cat.breeds[0].name} des={cat.breeds[0].description} image={cat.url}
                           inShowPet={inShowCat} animal={'Cat'} setUpForm={setUpForm} openForm={openForm}/>)
                
                )}

              

            </Row>
        </Container>
   
            {itemAdded && 
                <Container className='item-added'>
                    <img src={tick} height="40vh"></img>
                    <h1>
                        Pet Added To Adoption Page
                    </h1>
                </Container>}
     
        <MyFooter/>
        </div>
       
    )
}


return (

    
    <div>
        <Resource path={webUrl}render = {render}/>
    </div>
)
}

export default transition(ShowCat);