import Col from 'react-bootstrap/Col';


import React from 'react';



const HowToCard = (props)=>
{
    return (

        <Col md={4} className='d-flex justify-content-center mb-5 pb-5 howToContainer'>
        <div class="d-flex w-75 text-white howToCard rounded-5 shadow-lg" style={{backgroundImage:`url(${props.Background})`}}>
          <div class="d-flex flex-column p-5 pb-3 sub-color text-shadow-1">
            
            
            <h2 class="pt-5 mt-5 mb-4 display-4 lh-1 fw-bolder">{props.header}</h2>
            <ul class="d-flex list-unstyled mt-auto">
              <li class="me-auto">
              <p className='fw-bold'>{props.para} </p>
              </li>
            </ul>
          </div>
        </div>
    </Col>
    )

}

export default HowToCard;