import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import heart from '../svg/heart.svg';
import love from '../svg/love.svg';
import care from '../svg/care.svg'

const Features=()=>{
    
    return (
        
        <Container className='px-4 py-5 mt-5 feats w-75'>
            <h2 className=' text-center mb-3'>Reasons To Adopt With Us</h2>
            <Row className='py-5 row-cols-1 row-cols-lg-3  text-center'>

                <Col className='d-flex flex-column align-items-center'>
                   <img className='features-svg' src={heart} alt='sunheart'></img>
                    <h2>Brighten Up Your Life</h2>
                    <p className='lead'>Animals Bring In Laughter And Joy Into Our Lives Through Their Micro-Actions</p>
                </Col>
         

                <Col className='d-flex flex-column align-items-center'>
                <img className='features-svg' alt='trust' src={love}></img>
                    <h2>Trusted</h2>
                    <p className='lead'>Certified By The SPCA And Approved By The Government</p>
                </Col>


                <Col className='d-flex flex-column align-items-center'>
                <img className='features-svg' alt='walkwith' src={care}></img>
                    <h2>Never Alone Again</h2>
                    <p className='lead'> Never Be Lonely Again With The Company Of These Furry Friends</p>
                </Col>

            </Row>
        </Container>
    );
};





export default Features;


  
