import Container from 'react-bootstrap/Container';
import {React, useState,useContext } from 'react';
import { Context } from '../App';
import Row from 'react-bootstrap/Row';
import MiniCard from './MiniCard';
import { Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import CentralForm from './CentralForm';
import MyFooter from './Footer';
import transition from './transition';
const AdoptionPage =()=>{

    const {selectedPets} = useContext(Context);
    const [openAdoptForm,setAdoptForm] = useState(false);

    return (
        <div className='adopt-page'>

        {openAdoptForm && <CentralForm openAdoptForm={openAdoptForm} 
                                        setAdoptForm={setAdoptForm} 
                                        position={'absolute'}
                                        heading={'Adopt'}
                                    
                                        />}

          {selectedPets.length >0 && 
          <div class="adopt-head position-relative p-3 my-5 p-md-5 ">
                <div class="col-md-5 p-lg-5 mx-auto my-5 text-info">

                    <h1 class="display-4 fw-bold ">Thank You So Much For Your Interest!</h1>
                    <p class="lead fw-bold">But Before You Go......</p>
                    <Button className='btn btn-info text-light fw-bold' onClick={()=>{setAdoptForm(!openAdoptForm)}}>Fill Up Particulars</Button>
                </div>
            </div>}

            {selectedPets.length===0 && <div class="adopt-head position-relative p-3 my-5 p-md-5 ">
                <div class="col-md-5 p-lg-5 mx-auto my-5 ">

                    <h1 class="display-4 fw-bold ">Oops, You Do Not Have Any Selected Pets Currently!</h1>
                    <p class="lead fw-bold">Maybe You Might Want To Look At Our Gallery......</p>
                    <Button as={Link} to='/showCat'>Cats Gallery</Button>
                    <Button as={Link} className='ms-4' to='/showDog'>Dogs Gallery</Button>
                </div>
            </div>}
            


        <Container className='adopt-gallery pb-5 mb-5'>
            
            <Row>
            {selectedPets.map((pet)=>(
                <MiniCard name={pet.name} des={pet.des} image={pet.image}/>)
            
                )}
            </Row>
        </Container>

            <MyFooter/>
        </div>
    )
}

export default transition(AdoptionPage);